body {
  font-family: Arial, sans-serif;
  background-color: #f0f4f8;
  color: #333;
  margin: 0;
  padding: 0;
}

.header {
  background-color: #4caf50; /* Natural green */
  color: #fff;
  padding: 50px;
  text-align: center;
}

.navbar {
  display: flex;
  justify-content: space-around;
  background-color: #333; /* Dark grey */
  padding: 10px;
}

.navbar a {
  color: #fff;
  text-decoration: none;
  padding: 10px;
  font-weight: bold;
}

.navbar a:hover {
  background-color: #575757; /* Medium grey */
}

.main-content {
  padding: 20px;
}

.hero {
  background-color: #8bc34a; /* Light green */
  padding: 100px;
  color: #fff;
  text-align: center;
}

.hero h2 {
  margin: 0;
  font-size: 2.5em;
}

.form-container {
  background-color: #fff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  margin: 20px 0;
}

.form-group {
  margin-bottom: 15px;
}

.form-group label {
  display: block;
  margin-bottom: 5px;
}

.form-group input,
.form-group select {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.form-group button {
  background-color: #4caf50; /* Natural green */
  color: #fff;
  border: none;
  padding: 10px 20px;
  border-radius: 4px;
  cursor: pointer;
}

.form-group button:hover {
  background-color: #388e3c; /* Darker green */
}

.calendar {
  margin-top: 20px;
  background-color: #fff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.calendar h3 {
  margin-bottom: 15px;
}

.footer {
  background-color: #333; /* Dark grey */
  color: #fff;
  text-align: center;
  padding: 10px;
  position: fixed;
  bottom: 0;
  width: 100%;
}
